.detail__container {
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
  0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  padding: 20px;
  border-radius: 5px;
  background-color: white;
  margin: 0 auto;
  margin-top: 100px;
  width: 100%;
  max-width: 400px;
  font-weight: 300;
}
.detail__container img{
  width: 100%;
}
.detail__container .title,
.detail__container .year,
.detail__container .summary{
  display: block;
  width: 100%;
}
.detail__container .title{
  font-size: 24px;
  color:#000;
  font-weight: 600;
}
.detail__container .genres{
  font-size: 12px;
  color: rgb(58, 58, 58);
}
.detail__container h4{
  float:left;
  margin: 0 10px;
}
.detail__container .year{
  float: right;
}

.detail__container span:first-child {
  font-size: 20px;
}

.detail__container span:last-child {
  display: block;
  margin-top: 10px;
}