a{
    text-decoration: none;
}
.movies .movie {
    width: 45%;
    background-color: #000000a9;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);

}

.movie img {
    position: relative;
    top: -50px;
    max-width: 120px;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}
.movie img:hover {
    transform: scale(1.2);
    -webkit-transform: scale(1.2);
    -moz-transform: scale(1.2);
    -ms-transform: scale(1.2);
    -o-transform: scale(1.2);
}


.movie .movie__year,.movie .movie__genres, .movie p{
    color:rgba(230, 230, 230, 0.767);
}

.movie .movie__title, .movie .movie__year {
    color:rgb(255, 255, 255);
    margin: 0;
    font-weight: 800;
}

.movie .movie__title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #ffffff;
    text-shadow: 1px 2px 3px rgba(189, 189, 189, 0.678);
}

.movie .movie__genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    margin: 5px 0px;
}

.movie__genres li, .movie .movie__year {
    margin-right: 10px;
    font-size: 14px;
}

@media screen and (max-width:1090px){
    .movies .movie {
        width: 100%;
        margin-bottom: 70px;
        font-weight: 300;
        padding: 20px;
        border-radius: 5px;
        box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
          0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
      }
      
      .movies .movie a {
          display: grid;
          grid-template-columns: minmax(150px, 1fr) 2fr;
          grid-gap: 20px;
          text-decoration: none;
          color: inherit;
        }
    .movies .movie .movie__data {
        overflow: auto;
    }
}